import React from 'react';
import './App.css';
import Box2Sections from './Box2Sections';
import Box from './Box';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sections: '2',
      length: 115,
      width: 57.5,
      height: 25,
      section1length: 82.5,
      thickness: 1.4
    };
    this.handleSectionsChange = this.handleSectionsChange.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    setTimeout(() => this.updateDownloadLinks(), 100);
  }

  handleSectionsChange(changeEvent) {
    this.setState({ sections: changeEvent.target.value });
    setTimeout(() => this.updateDownloadLinks(), 100);
  }

  updateDownloadLinks() {
    this.setState({topName: `top-${this.state.length}-${this.state.width}-${this.state.height}.svg`});
    const topSvg = document.getElementById('top_svg').innerHTML;
    const topData = new Blob([topSvg], {type: 'text/plain'});
    const topUrl = window.URL.createObjectURL(topData);
    this.setState({topUrl: topUrl});
    this.setState({topWidth: document.getElementById('top_svg').dataset.width});

    this.setState({bottomName: `bottom-${this.state.length}-${this.state.width}-${this.state.height}.svg`});
    const bottomSvg = document.getElementById('bottom_svg').innerHTML;
    const bottomData = new Blob([bottomSvg], {type: 'text/plain'});
    const bottomUrl = window.URL.createObjectURL(bottomData);
    this.setState({bottomUrl: bottomUrl});
    this.setState({bottomWidth: document.getElementById('bottom_svg').dataset.width});
  }

  handleInputChange(event) {
    const target = event.target;
    const name = target.id;
    this.setState({ [name]: parseInt(event.target.value) });

    setTimeout(() => this.updateDownloadLinks(), 100);
  }

  render() {
    const start = this.state.height * 2 + this.state.thickness * 2 + 10;
    return (
      <div className='container'>
        <div className='leftPanel'>
          <div>
            <input
              type='radio'
              id='1section'
              name='section'
              value='1'
              checked={this.state.sections === '1'}
              onChange={this.handleSectionsChange}
            />
            <label htmlFor='1section'>1 section</label>
            <br />
            <input
              type='radio'
              id='2section'
              name='section'
              value='2'
              checked={this.state.sections === '2'}
              onChange={this.handleSectionsChange}
            />
            <label htmlFor='2section'>2 section</label>
          </div>
          <div>
            <label htmlFor='length'>Length</label>
            <br />
            <input
              id='length'
              type='number'
              size='5'
              min='0'
              max='250'
              value={this.state.length}
              onChange={this.handleInputChange}
            />
            mm
          </div>
          <div>
            <label htmlFor='width'>Width</label>
            <br />
            <input
              id='width'
              type='number'
              size='5'
              min='0'
              max='250'
              value={this.state.width}
              onChange={this.handleInputChange}
            />
            mm
          </div>
          <div>
            <label htmlFor='height'>Height</label>
            <br />
            <input
              id='height'
              type='number'
              size='5'
              min='0'
              max='150'
              value={this.state.height}
              onChange={this.handleInputChange}
            />
            mm
          </div>
          {this.state.sections === '2' ? (
            <div>
              <label htmlFor='section1length'>Section 1 size</label>
              <br />
              <input
                id='section1length'
                type='number'
                size='5'
                min='0'
                max='250'
                    value={this.state.section1length}
                onChange={this.handleInputChange}
              />
              mm
            </div>
          ) : (
            <div />
          )}
          <div>
            <label htmlFor='thickness'>Thickness</label>
            <br />
            <input
              id='thickness'
              min='0'
              max='20'
              size='5'
              value={this.state.thickness}
              onChange={this.handleInputChange}
            />
            mm
          </div>
          <a href={this.state.topUrl} download={this.state.topName}>Download top box</a><br/> 
          (Width: {this.state.topWidth}mm)<br/>
          <br/>
          <a href={this.state.bottomUrl} download={this.state.bottomName}>Download bottom box</a><br/>
          (Width: {this.state.bottomWidth}mm)<br/>
        </div>
        <div className='rightPanel'>
          {this.state.sections === '1' ? (
            <Box
              width={this.state.width}
              length={this.state.length}
              height={this.state.height}
              thickness={this.state.thickness}
              startx={start}
              starty={start}
            />
          ) : (
            <Box2Sections
              width={this.state.width}
              length={this.state.length}
              height={this.state.height}
              thickness={this.state.thickness}
              startX={start}
              startY={start}
              section1length={this.state.section1length}
            />
          )}
        </div>
      </div>
    );
  }
}

export default App;
